<template>
	<section id="banner">
      <span class="title">{{$t('banner.banner')}}</span>
      <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
        <el-form :inline="true" :model="bannerConfig">
        	<el-form-item :label="$t('playSort')">
        		<el-select v-model="bannerConfig.displayOrder" :placeholder="$t('All')">
    		      <el-option :label="$t('leftToRight')" value="0"></el-option>
    		      <el-option :label="$t('rightToLeft')" value="1"></el-option>
		        </el-select>
  	      </el-form-item>
        	<el-form-item :label="$t('playTime')">
        		<el-input v-model="bannerConfig.displayInterval"  :placeholder="$t('ptimer')"></el-input>
        	</el-form-item>
          <el-form-item>
            <el-button class="warning" v-on:click="basicBannerConfig">{{$t('previewFunction')}}</el-button>
          </el-form-item>
          <el-form-item>
           <el-button class="warning box"  >{{$t('newPictures')}}
           <input type="file" multiple id="file"   @change="fileChange($event)" >  
           <label for="file"></label>
           </el-button>
          </el-form-item>
        </el-form>
      </el-col>
     	 <el-table :data="userList"    style="width: aotu;"   >
             <el-table-column  prop="orders" :label="$t('banner.thead[0]')">
             </el-table-column>
             <el-table-column prop="bannerUrl" :label="$t('banner.thead[1]')">
               <template slot-scope="scope">
                  <img  :src="scope.row.bannerUrl" alt=""  style="width: 50px;height: 50px">
               </template>
             </el-table-column>
             <el-table-column :label="$t('service.thead[2]')">
                <template slot-scope="scope">
                	<el-button size="mini" @click="prev(scope.$index,scope.row)">{{$t('banner.do[0]')}}</el-button>
                  <el-button size="mini" @click="next(scope.$index,scope.row)">{{$t('banner.do[1]')}}</el-button>
                  <el-button size="mini"  @click.native.prevent="deleteRow(scope.$index,scope.row,userList)">{{$t('banner.do[2]')}}</el-button>
                </template>
             </el-table-column>
        </el-table>
        <el-button type="info" @click.once="updateFile" >{{$t('sureChange')}}banner</el-button> 
        
        <!-- 轮播弹框 -->

        <el-dialog :visible.sync="wrapper">
            <template>
              <el-carousel indicator-position="outside" :interval="bannerConfig.displayInterval">
                <el-carousel-item v-for="item in userList" :key="item.orders">
                  <img :src="item.bannerUrl"  alt="">
                </el-carousel-item>
              </el-carousel>
            </template>
        </el-dialog>

  </section>
</template>
   
<script>
import {ajax,dateStr2GmtTimeStamp,getDataYear} from '../../common/js/common.js'

export default {
   data(){
	     return{
        wrapper:false,
        shunXu:"",
        deleteId:[],
        input:"",
        select:"",
       	userList:[],
        imgList:[],
        bannerConfig:{
            displayOrder:"",
            displayInterval:3000,
            },
        pic:[],
        doDate:[],
        n:0,
        index:1,
        lengthBiao:"",
        fileIndex:1,
		  }
	 },

  methods:{
    fileChange(el) {
        if (!el.target.files[0].size) return;
        this.fileList(el.target);
        el.target.value = ''

      },
    fileList(fileList) {
        let files = fileList.files;
        for (let i = 0; i < files.length; i++) {
          //判断是否为文件夹
          if (files[i].type != '') {
            this.fileAdd(files[i]);

          } else {
            //文件夹处理
            this.folders(fileList.items[i]);
          }
        }
      },
    //文件夹处理
    folders(files) {
        let _this = this;
        //判断是否为原生file
        if (files.kind) {
          files = files.webkitGetAsEntry();
        }
        files.createReader().readEntries(function (file) {
          for (let i = 0; i < file.length; i++) {
            if (file[i].isFile) {
              _this.foldersAdd(file[i]);
            } else {
              _this.folders(file[i]);
            }
          }
        })
      },
    foldersAdd(entry) {
        let _this = this;
        entry.file(function (file) {
          _this.fileAdd(file)
        })
      },
    fileAdd(file) {
        //判断是否为图片文件
        if (file.type.indexOf('image') == -1) {
          file.src = 'wenjian.png';
          this.imgList.push({
            file
          });
        } else {
          let reader = new FileReader();
          let image = new Image();
          let _this=this;
          reader.readAsDataURL(file);
          reader.onload = function () {
            file.src = this.result;
            image.onload=function(){
              let width = image.width;
              let height = image.height;
              file.width=width;
              file.height=height;
              _this.imgList.push({file});

              // console.log(_this.imgList)
              _this.userList.push({
                "bannerUrl":file.src,"orders": _this.userList.length+1,"fileOrder":_this.index
              });
              _this.index++;
              // console.log( _this.userList);
            };
            image.src= file.src;
          }
        }
      },


    //有个按钮来提交更新后的东西的 (banner图的更新操作及顺序和删除操作)
    updateFile:function(){
      let arr = this.userList;
      arr.forEach((item,i,arr) =>{
        arr[i].orders = i+1;
        if(arr[i].fileOrder){
            arr[i].fileOrder =this.fileIndex;
            this.fileIndex++;
          }else{
            return ;
          }
          
        
      })
      let formData = new FormData();

      let data=JSON.stringify(this.userList);
      formData.append("moveJson",data);

      let data2 = JSON.stringify(this.deleteId)
      formData.append("deleteJson",data2);
      // console.log(this.userList)
      for(var i=0;i<this.imgList.length;i++){
        
       formData.append("pics",this.imgList[i].file)
       
      }
     /* console.log("这是moveJson里的数组")
      console.log(this.userList)
      console.log("这是上传的文件")
      console.log(this.imgList)*/
      let that = this;
       this.axios.post(this.ApiConfig.bannerConfig.updateBanner,formData,{
        headers:{
          'Content-Type':'multipart/form-data;charset=UTF-8',
          Authorization:sessionStorage.getItem('tokenId')
        }
      }).then(function(res){
        // console.log(res.data)
        if(res.data.code == 200){
          // console.log("成功了de")
          // console.log(res.data)
          that.$notify({
            title:"提示",
            message:"改变成功",
            type:"success"
          })
        }else{
          that.$notify({
            title:"提示",
            message:res.data.message,
            type:"warning"
          })
          return false;
        }
      }).catch(function(error){
        console.log(error)
      });
    },

    // 查询后台轮播图的所有信息
    queryBanners:function(){
      ajax(this,this.ApiConfig.bannerConfig.queryBanners,{},(res) => {
        if(res.code == 200){
           // console.log(res)
        this.userList = res.data.bannerTables;
        this.lengthBiao = this.userList.length;
        var arrWant=[];
        var arr =this.userList;

        arr.forEach((item,i,arr) =>{
          arrWant.push(arr[i].orders)
        })
        // console.log("要处理顺序的数组")
        // console.log(arrWant)
        // 数组从小到大排序
        for(var i=0;i<arr.length;i++){
          for(var j=0;j<arr.length-i;j++){
            if(arrWant[j]>arrWant[j+1]){

              var a=arrWant[j];
              var b= arr[j];
              arrWant[j] =arrWant[j+1];
              arr[j]=arr[j+1];
              arrWant[j+1] =a;
              arr[j+1] =b;
            }
          }
        }
        /*
          console.log(arr)
          console.log(this.userList)
          console.log(this.userList)
        */
        this.bannerConfig.displayOrder = res.data.bannerConfig.displayOrder;
        if(this.bannerConfig.displayOrder==1){
          this.bannerConfig.displayOrder = "从右往左"
        }else if(this.bannerConfig.displayOrder==0){
          this.bannerConfig.displayOrder = "从左往右"

        }
        this.bannerConfig.displayInterval = res.data.bannerConfig.displayInterval;
      }else{
        this.$notify({
          title:"提示",
          message:res.message,
          type:'warning'
        })
        return false;
      }
      },"POST")
    },
    // banner图的基本参数配置(播放顺序和播放间隔)
    basicBannerConfig:function(){
      this.wrapper=true;
      if(this.bannerConfig.displayOrder=="从右往左"){
          this.bannerConfig.displayOrder = 1
        }else if(this.bannerConfig.displayOrder=="从左往右"){
          this.bannerConfig.displayOrder = 0
        }
      ajax(this,this.ApiConfig.bannerConfig.basicBannerConfig,this.bannerConfig,(res) => {
        if(res.code == 200){
          // console.log(res)
        }else{
          this.$notify({
            title:'提示',
            message:res.message,
            type:'warning'
          })
          return false;
        }
        
      },"POST")
    },
    // 上下移动及删除
    prev:function(index,row){
      this.bool=false;
      // console.log(index)
      if(index==0){
        this.$notify({
          title:"提示",
          message:"已经是第一个了",
          type:"warning"
        })
        return false;
      }else{
         
        // console.log(index)
          let num = this.userList[index-1];
          this.userList[index-1]= this.userList[index];
          this.userList[index] = num;
      this.userList.push("");
      this.userList.pop("");
      }
    },
    next:function(index,row){
      this.bool =false;
      if(index==this.userList.length-1){
        this.$notify({
          title:"提示",
          message:"已经是最后一个了",
          type:"warning"
        })
        return false;
      }else{
        let bridge =  this.userList[index+1];
      this.userList[index+1]=row;
      this.userList[index]=bridge;
      this.userList.push("");
      this.userList.pop("");
      }
    },
    deleteRow:function(index,row,rows){
     
      // console.log(row)
      if(row.id){
        this.deleteId.push(row.id)
          rows.splice(index, 1);
          // console.log(111)
      }else{
        // this.userList.splice(row.orders,1)
        
        this.imgList.splice(row.fileOrder-1,1)
        // console.log("不生气不生气")
        // console.log(this.imgList)

        rows.splice(index, 1);
        return ;
      }
    },
  },
  
  mounted:function(){
    this.queryBanners();
  },
}


</script>
<style scoped lang="postcss">
  .box{
    position: relative;

    input{
      position: absolute;
      left: -9999px;
    }
    label{
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 10;
    }
  }
</style>

<style>
#banner{
   .toolbar .el-form-item .warning{
    /* width: 85px; */
   }
  .el-select .el-input {
    width: 105px;
  }
  .el-upload-list{
    display: none;
  }
  .el-carousel__item img {
    opacity: 0.75;
    margin: 0;
    max-width:100%;
    height:auto;
  }
  .el-dialog{
      width: 600px;
    }
 /*  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  } */
}

</style>






